.vh-g-primary {
  background: #ffc800;
  background: -webkit-gradient(left top, right bottom, color-stop(0%, #ffc800), color-stop(100%, #bf9600));
  background: linear-gradient(135deg, #ffc800 0%, #bf9600 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffc800', endColorstr='#bf9600', GradientType=1 ); }

.vh-g-primary-d {
  background: #806400;
  background: -webkit-gradient(left top, right bottom, color-stop(0%, #806400), color-stop(100%, #ffc800));
  background: linear-gradient(135deg, #806400 0%, #ffc800 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#806400', endColorstr='#ffc800', GradientType=1 ); }

.vh-g-grey {
  background: #666666;
  background: -webkit-gradient(left top, right bottom, color-stop(0%, #666666), color-stop(100%, black));
  background: linear-gradient(135deg, #666666 0%, black 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#666666', endColorstr='#000000', GradientType=1 ); }

.vh-g-grey-d {
  background: #333333;
  background: -webkit-gradient(left top, right bottom, color-stop(0%, #333333), color-stop(100%, black));
  background: linear-gradient(135deg, #333333 0%, black 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#333333', endColorstr='#000000', GradientType=1 ); }

.process-loading, .process-loading-two {
  position: absolute;
  top: 0;
  bottom: 0;
  left: -100%;
  right: 0;
  height: 100%;
  width: 100%;
  background-color: #fff;
  z-index: 10;
  transition: all 0.5s linear; }

.process-loading-two {
  visibility: hidden;
  left: 0;
  background-color: rgba(255, 255, 255, 0.85); }

.process-loading-three {
  visibility: hidden;
  top: 10;
  bottom: 10;
  left: 10;
  right: 10;
  opacity: 0;
  background-color: rgba(255, 255, 255, 0.85); }

.add-loading {
  left: 0;
  opacity: 1;
  visibility: visible; }

.spinner {
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: block;
  width: 75px;
  height: 75px;
  border-radius: 50%; }
  .spinner:before, .spinner:after {
    position: absolute;
    content: '';
    display: block;
    height: 100%;
    width: 100%;
    border-radius: 50%;
    border: 5px solid #eee; }
  .spinner:before {
    background-color: #ffd700;
    border: 0;
    transform: scale(-0.3);
    animation: spinnerB 1s linear infinite; }
  .spinner:after {
    bottom: 0;
    left: 0;
    border-bottom-color: #ffd700;
    animation: spinner 0.5s linear infinite; }

@keyframes spinner {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes spinnerB {
  0% {
    transform: scale(-0.3);
    opacity: 1; }
  70% {
    opacity: 0;
    transform: scale(0.75); }
  100% {
    transform: scale(-0.3); } }

